import {updateCartDB} from "../database";
import {clearUserCart} from "../database";

export default {
  state: () => ({
    products: [],
  }),

  getters: {
    productsInCart(state) {
      return state.products;
    },

    totalQuantityOfProducts(state){
      let totalQuantity = state.products.reduce(
        (accumulator, current) =>
          accumulator + current.quantity,
        0
      );
      return totalQuantity;
    },

    subtotal(state) {
      let subtotal = state.products.reduce(
        (accumulator, current) =>
          accumulator + current.price * current.quantity,
        0
      );
      return subtotal;
    },

    total(state){
      let subtotal = state.products.reduce(
        (accumulator, current) =>
          accumulator + current.price * current.quantity,
        0
      );
      let total = subtotal + (subtotal * 14) / 100;
      return total
    },
  },

  mutations: {
    cartProductsList(state, product) {
      for(let i=0; i < product.length; i++){
        state.products.push(product[i])
      }
    },

    clearCart(state){
      let products = []
      state.products = products
      clearUserCart();
    },

    addToCart(state, product) {
      // console.log("Search existing product in store");
      let findProduct = state.products.find((data) => data.id === product.id);

      if (!findProduct) {
        product.quantity = 1;
        state.products.push(product);
      } else {
        findProduct.quantity += 1;
      }
      // console.log("end search in store");
      updateCartDB();
    },

    incrementProductQuantity(state, product) {
      let findProduct = state.products.find((data) => data.id === product.id);

      if(findProduct){
        findProduct.quantity += 1;
      }
      updateCartDB();
    },

    async removeFromCart(state, product) {
      // console.log("Search existing product in store");
      let findProduct = state.products.find((data) => data.id === product.id);

      if (findProduct) {
        state.products.splice(state.products.indexOf(findProduct), 1);
      }
      // console.log("End search in store");
      updateCartDB();
    },

    decrementProductQuantity(state, product) {
      let findProduct = state.products.find((data) => data.id === product.id);

      if (findProduct && findProduct.quantity > 1) {
        findProduct.quantity = findProduct.quantity - 1;
      }
      updateCartDB();
    }
  },
};
