import {addOrder} from "../database";
import {clearUserOrders} from "../database";

export default {
  state: () => ({
    orderN: [],
  }),

  getters: {
    getOrders(state) {
      return state.orderN;
    },
  },

  mutations: {
    setProductsPerOrder(state, order) {
      state.orderN.push(order);
      addOrder(order);
      // console.log("Order store:", order);
    },

    emptyOrders(state){
      let orders = []
      state.orderN = orders
      clearUserOrders();
    }
  },
};
